import React from 'react';
import Container from '../components/container';
import { Input } from '../components/uiComponent';

const Upload = () => {
  return (
    <Container>
      <div>미리보기 이미지</div>
      <form>
        <Input type="file"></Input>
        <Input type="text"></Input>
      </form>
    </Container>
  );
};

export default Upload;
